body
{
    font-family: 'Arial';

    background: #42ab9e;
}

.main
{
    width: 90%;
    margin: 0 auto;
}

.welcome
{
    font-family: 'Arial';
    font-size: 50px;

    position: relative;

    margin: 0 0 20px 0;
    padding-top: 40px;

    text-align: center;

    color: #fff;
}

.header
{
    font-family: 'Arial';
    font-size: 25px;

    width: 30%;
    margin: 50px auto;

    padding: 15px 10px;

    text-align: center;

    color: #42ab9e;
    background: #fff;
    -webkit-box-shadow: 2px 20px 83px -18px rgba(56,56,56,1);
            box-shadow: 2px 20px 83px -18px rgba(56,56,56,1);
}

.data
{
    margin: 0 auto;
}

.data div
{
    margin-top: 60px;
}

a.entry_btn
{
    display: block;

    width: 20%;
    margin: 40px auto;
    padding: 20px;

    -webkit-transition: .9s;
       -moz-transition: .9s;
        -ms-transition: .9s;
         -o-transition: .9s;
    text-align: center;
    text-decoration: none;
    letter-spacing: 2px;

    color: #fff;
    border-radius: 50px;
    border: 2px solid #fff;
    -webkit-box-shadow: 2px 20px 83px -18px rgba(56,56,56,.41);
            box-shadow: 2px 20px 83px -18px rgba(56,56,56,.41);
}

a.entry_btn:hover
{
    -webkit-transition: .5s;
       -moz-transition: .5s;
        -ms-transition: .5s;
         -o-transition: .5s;

    opacity: 1;
    color: #42ab9e;
    border: 2px solid #fff;
    background: #fff;
}

.data div ul
{
    margin: 50px 0;
}

.showData
{
    margin: 40px 0;
    display: inline-block;

    width: 100%;

    background: #fff;
    -webkit-box-shadow: 2px 20px 83px -18px rgba(56,56,56,1);
            box-shadow: 2px 20px 83px -18px rgba(56,56,56,1);
}

.showData ul {
  padding: 0 40px;
}

.showData li
{
    margin: 10px 20px;

    list-style: none;
}

.showData h2
{
    margin: 0 0 15px 0;
    padding: 20px 60px;

    background: #eee;
}

.omw {
  height: 55px;
  background: #eee;
  border-radius: 5px;
  float: left;
  padding: 15px 30px;
  margin-bottom: 20px !important;
}

.load-section {
  color: #fff;
  text-align: center;
  margin: 0 auto;
  position: absolute;
  left: 0;
  right: 0;
  top: 250px;
}

input[type='checkbox'] {
  -webkit-appearance: none;
  width: 30px;
  height: 30px;
  background: #fff;
  border: 1px solid #888;
  border-radius: 5px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

input[type='checkbox']:checked {
  background: #000;
  background-image: url("data:image/jpeg;base64,");
  background-size: 30px;
  background-position: -1px -1px;
  background-repeat: no-repeat;
  border: 1px solid transparent;
}

.inputBox {
  display: inline-block;
  margin-left: 10px;
  position: relative;
  top: 10px;
}

.inputBox:focus,
input[type='checkbox']:focus {
  outline: none;
}


